import React, { useEffect, useState } from 'react'
import axios from 'axios'
import EquipoId from './EquipoId'
import { Link } from 'react-router-dom'

const EquipoMil = () => {
    const [equipo, setEquipo] = useState([])
    const [loading, setLoading] = useState(true)
    useEffect(() => {
        axios.get('https://EquiposLaboratorio.pablojurado2.repl.co/api/equipomilwaukee').then(function (response) {
            setEquipo(response.data)
            console.log(response.data)
            setLoading(false)
        });
    }, [])
    return (
        <>
        {loading ? <div className="bgwhite uk-height-large uk-flex uk-flex-center uk-text-warning uk-text-bold" uk-spinner="ratio: 3"></div> :(
        <div className="bgwhite ">
            <h1 className="uk-text uk-text-center uk-padding uk-heading-bullet">MEDIDORES DE MILWAUKEE ELECTRONICS</h1>
            <p>MEDIDORES DE MILWAUKEE ELECTRONICS
                Milwaukee es un fabricante mundial dinámico de Instrumentación Electroquímica para el análisis de Aguas al medir pH, Redox, Conductividad, Salinidad, Ox�geno Disuelto, Temperatura, Turbiedad, Cloro, Amoniaco, Cobre, Cloruro, Fosfato, Hierro, etc. Los Instrumentos Milwaukee son para todos los mercados donde se requieren la medición de la calidad del Agua: Laboratorios, Alimentación y Bebidas, Medio Ambiente, Educación y Administración, Tratamiento de Aguas y Desag�es, Farmacéutica y Biotecnología, Química, Agricultura y Horticultura, Hidropónica, Acuarios, Piscinas, etc..</p>
            {equipo.map((e) =>





                <div>
                    <div class="uk-card uk-card-default">
                        <div class="uk-card-media-top">
                            <Link to={'/equipo/' + e._id}><img src={e.imagen} width="300px" height="200px" alt="" /></Link>

                        </div>
                        <div class="uk-card-body">
                            <h3 class="uk-card-title">{e.nombre}</h3>
                            {/*   <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.</p> */}
                        </div>
                    </div>
                </div>
            )}

        </div>
        )};
        </>
    )
}

export default EquipoMil